import React, { useState, useEffect, useMemo } from 'react';
import { FaSearch, FaTimes } from 'react-icons/fa';
import { ClipLoader } from 'react-spinners';

// Error Boundary Component
class ErrorBoundary extends React.Component {
    state = { hasError: false, errorMessage: '' };

    static getDerivedStateFromError(error) {
        return { hasError: true, errorMessage: error.message };
    }

    componentDidCatch(error, errorInfo) {
        console.error(`[${new Date().toISOString()}] ErrorBoundary caught an error:`, error, errorInfo);
    }

    render() {
        if (this.state.hasError) {
            return (
                <div className="text-center text-red-600 p-6 bg-red-50 rounded-lg">
                    <h2 className="text-lg font-semibold">Une erreur est survenue</h2>
                    <p className="text-sm">{this.state.errorMessage || 'Veuillez réessayer plus tard ou contactez le support.'}</p>
                    <button
                        onClick={() => window.location.reload()}
                        className="mt-4 px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 text-sm"
                    >
                        Réessayer
                    </button>
                </div>
            );
        }
        return this.props.children;
    }
}

const RangementLE = () => {
    const [data, setData] = useState([]);
    const [searchTerm, setSearchTerm] = useState('');
    const [currentPage, setCurrentPage] = useState(1);
    const [totalPages, setTotalPages] = useState(1);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);
    const [isSlow, setIsSlow] = useState(false);
    const [cache, setCache] = useState({});
    const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000';

    // Fetch data with SSE
    const fetchData = useMemo(
        () => (search, page, isPrefetch = false) => {
            const cacheKey = `${search || 'no-search'}-${page}`;
            if (cache[cacheKey] && !isPrefetch) {
                console.log(`[${new Date().toISOString()}] Using cached data for ${cacheKey}`);
                setData(cache[cacheKey].data);
                setTotalPages(cache[cacheKey].totalPages);
                setLoading(false);
                setIsSlow(false);
                return;
            }

            console.log(`[${new Date().toISOString()}] Fetching data for page ${page}, search: ${search || 'none'}`);
            const source = new EventSource(
                `${API_URL}/rangement-le?search=${encodeURIComponent(search || '')}&page=${page}`
            );

            if (!isPrefetch) {
                setLoading(true);
                setError(null);
                setData([]);
                setIsSlow(false);
            }

            const slowTimeout = setTimeout(() => {
                if (!isPrefetch) setIsSlow(true);
            }, 3000);

            source.onmessage = (event) => {
                const response = JSON.parse(event.data);
                console.log(`[${new Date().toISOString()}] Received SSE event:`, response);

                if (response.error) {
                    if (!isPrefetch) {
                        setError(response.details || 'Échec du chargement des données');
                        setLoading(false);
                        setIsSlow(false);
                    }
                    source.close();
                    clearTimeout(slowTimeout);
                    return;
                }

                if (response.batch) {
                    if (!isPrefetch) {
                        console.log(`[${new Date().toISOString()}] Adding ${response.data.length} rows to data`);
                        setData(prev => [...prev, ...response.data]);
                        setTotalPages(response.totalPages);
                    } else {
                        setCache(prev => ({
                            ...prev,
                            [cacheKey]: { data: response.data, totalPages: response.totalPages }
                        }));
                    }
                }

                if (response.complete) {
                    if (!isPrefetch) {
                        setLoading(false);
                        setIsSlow(false);
                    }
                    source.close();
                    clearTimeout(slowTimeout);
                    console.log(`[${new Date().toISOString()}] Stream complete`);
                }
            };

            source.onerror = () => {
                if (!isPrefetch) {
                    setError('Erreur de connexion au serveur. Vérifiez que le serveur est en cours d’exécution.');
                    setLoading(false);
                    setIsSlow(false);
                }
                source.close();
                clearTimeout(slowTimeout);
                console.log(`[${new Date().toISOString()}] SSE connection error`);
            };

            return () => source.close();
        },
        [cache]
    );

    // Pre-fetch next page
    useEffect(() => {
        if (currentPage < totalPages) {
            fetchData(searchTerm, currentPage + 1, true);
        }
    }, [currentPage, totalPages, searchTerm]);

    // Fetch data on search or page change
    useEffect(() => {
        const cleanup = fetchData(searchTerm, currentPage);
        return cleanup;
    }, [searchTerm, currentPage]);

    const handleNextPage = () => {
        if (currentPage < totalPages) {
            setCurrentPage(currentPage + 1);
            console.log(`[${new Date().toISOString()}] Navigating to page ${currentPage + 1}`);
        }
    };

    const handlePrevPage = () => {
        if (currentPage > 1) {
            setCurrentPage(currentPage - 1);
            console.log(`[${new Date().toISOString()}] Navigating to page ${currentPage - 1}`);
        }
    };

    const handleClearSearch = () => {
        setSearchTerm('');
        setCurrentPage(1);
        setCache({});
        console.log(`[${new Date().toISOString()}] Search cleared`);
    };

    const tableRows = useMemo(() => {
        if (!Array.isArray(data) || data.length === 0) {
            console.log(`[${new Date().toISOString()}] No data to display`);
            return (
                <tr>
                    <td colSpan={11} className="py-4 px-4 border text-center text-gray-500 text-sm">
                        Aucune donnée disponible
                    </td>
                </tr>
            );
        }

        console.log(`[${new Date().toISOString()}] Rendering ${data.length} rows`);
        return data.map((item, index) => (
            <tr key={item.article_code || `item-${index}`} className="hover:bg-gray-50 transition-colors">
                <td className="py-2 px-4 border text-sm">{item.article_code || 'N/A'}</td>
                <td className="py-2 px-4 border text-sm whitespace-normal break-words">{item.description || 'N/A'}</td>
                <td className="py-2 px-4 border text-sm" style={{ whiteSpace: 'pre-line' }}>
                    {item.historical_locations || 'N/A'}
                </td>
                <td className="py-2 px-4 border text-sm">{item.final_location || 'N/A'}</td>
                <td className="py-2 px-4 border text-sm">{item.suitable_location || 'N/A'}</td>
                <td className="py-2 px-4 border text-sm">{item.Storage_Location || 'N/A'}</td>
                <td className="py-2 px-4 border text-sm">{item.Storage_location_Validé || 'N/A'}</td>
                <td className="py-2 px-4 border text-sm">{item.BIN_SAP || 'N/A'}</td>
                <td className="py-2 px-4 border text-sm">{item.quantity || 'N/A'}</td>
                <td className="py-2 px-4 border text-sm">{item.unit || 'N/A'}</td>
                <td className="py-2 px-4 border text-sm">{item.source_file || 'N/A'}</td>
            </tr>
        ));
    }, [data]);

    return (
        <ErrorBoundary>
            <div className="container mx-auto p-4 sm:p-6 max-w-full">
                <h1 className="text-2xl font-bold mb-6 text-gray-800">Guide de Rangement</h1>

                {/* Search Input */}
                <div className="relative mb-6">
                    <div className="flex items-center border rounded-lg bg-white shadow-sm">
                        <FaSearch className="ml-3 text-gray-400" />
                        <input
                            type="text"
                            placeholder="Rechercher par code article ou désignation"
                            className="w-full p-2 pl-3 border-none rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 text-sm"
                            value={searchTerm}
                            onChange={(e) => {
                                setSearchTerm(e.target.value);
                                setCurrentPage(1);
                                setCache({});
                                console.log(`[${new Date().toISOString()}] Search term updated: ${e.target.value}`);
                            }}
                            aria-label="Rechercher par code article ou désignation"
                        />
                        {searchTerm && (
                            <button
                                onClick={handleClearSearch}
                                className="p-2 text-gray-400 hover:text-gray-600"
                                aria-label="Effacer la recherche"
                            >
                                <FaTimes />
                            </button>
                        )}
                    </div>
                </div>

                {/* Pagination Controls */}
                <div className="flex justify-between items-center mb-6 flex-wrap gap-4">
                    <button
                        onClick={handlePrevPage}
                        disabled={currentPage === 1}
                        className="px-4 py-2 bg-blue-500 text-white rounded-lg disabled:bg-gray-300 disabled:cursor-not-allowed hover:bg-blue-600 transition-colors text-sm"
                        aria-label="Page précédente"
                    >
                        Précédent
                    </button>
                    <span className="text-sm text-gray-600">
                        Page {currentPage} sur {totalPages} ({data.length} enregistrements)
                    </span>
                    <button
                        onClick={handleNextPage}
                        disabled={currentPage === totalPages}
                        className="px-4 py-2 bg-blue-500 text-white rounded-lg disabled:bg-gray-300 disabled:cursor-not-allowed hover:bg-blue-600 transition-colors text-sm"
                        aria-label="Page suivante"
                    >
                        Suivant
                    </button>
                </div>

                {/* Loading and Error States */}
                {loading && (
                    <div className="flex justify-center items-center py-8">
                        <ClipLoader size={30} color="#3B82F6" />
                        <span className="ml-3 text-sm text-gray-600">
                            {isSlow ? 'Chargement des données en cours, réception des premiers résultats...' : 'Chargement des données...'}
                        </span>
                    </div>
                )}
                {error && (
                    <div className="text-center text-red-600 bg-red-50 p-4 rounded-lg text-sm">
                        {error}
                        <button
                            onClick={() => fetchData(searchTerm, currentPage)}
                            className="ml-4 px-3 py-1 bg-blue-500 text-white rounded hover:bg-blue-600 text-sm"
                        >
                            Réessayer
                        </button>
                    </div>
                )}

                {/* Data Table */}
                {!error && (
                    <div className="overflow-x-auto shadow-lg rounded-lg">
                        <table className="min-w-full bg-white border text-sm">
                            <thead>
                                <tr className="bg-gray-100 text-gray-700">
                                    <th scope="col" className="py-3 px-4 border-b text-left font-medium">Code Article</th>
                                    <th scope="col" className="py-3 px-4 border-b text-left font-medium">Désignation</th>
                                    <th scope="col" className="py-3 px-4 border-b text-left font-medium">Emplacements Historiques</th>
                                    <th scope="col" className="py-3 px-4 border-b text-left font-medium">Emplacement Final</th>
                                    <th scope="col" className="py-3 px-4 border-b text-left font-medium">Emplacement Convenable</th>
                                    <th scope="col" className="py-3 px-4 border-b text-left font-medium">Storage Location</th>
                                    <th scope="col" className="py-3 px-4 border-b text-left font-medium">Storage Location Validé</th>
                                    <th scope="col" className="py-3 px-4 border-b text-left font-medium">BIN SAP</th>
                                    <th scope="col" className="py-3 px-4 border-b text-left font-medium">Quantité</th>
                                    <th scope="col" className="py-3 px-4 border-b text-left font-medium">Unité</th>
                                    <th scope="col" className="py-3 px-4 border-b text-left font-medium">Source Fichier</th>
                                </tr>
                            </thead>
                            <tbody>{tableRows}</tbody>
                        </table>
                    </div>
                )}
            </div>
        </ErrorBoundary>
    );
};

export default RangementLE;